  /**
   * Ly danh sch qun huyn theo m tnh thnh
   * @param provinceCode m tnh thnh
   * @returns danh sch qun huyn hoc mng rng nu khng tm thy
   */
  getDistricts(provinceCode: number): any {
    const province = this.provinces.find(province => province.code === provinceCode);
    return province ? province.districts : [];
  }



  /**
   * Ly danh sch tnh thnh khng cha thng tin qun huyn
   * @returns danh sch tnh thnh khng cha thng tin qun huyn
   */
  getProvinces(): any {
    return this.provinces.map(province => {
      const { districts, ...provinceWithoutDistricts } = province;
      return provinceWithoutDistricts;
    });
  }

