import { Injectable } from '@angular/core';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from '../components/vh-component/vh-component';
import { LanguageService } from './language.service';
import { Router } from '@angular/router';


@Injectable({
  providedIn: 'root'
})
export class BranchService {

  constructor(
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private router: Router,
  ) {}


  /**
   * Kim tra xem c ang c nhiu chi nhnh khng
   * @returns true nu c nhiu chi nhnh, ngc li tr v false
   */
  checkUsingBranch(): boolean {
    return this.vhQuerySales.getlocalBranchs().length > 1 ? true : false
  }


  /**
   * Ly chi nhnh mc nh. Nu tn chi nhnh mc nh khng khp vi tn chi nhnh hin ti,
   * xa chi nhnh mc nh v t li chi nhnh mc nh l chi nhnh hin ti.
   * @returns Chi nhnh mc nh
   */
  getBranchDefault(): any {
    let branchDefault = this.vhQuerySales.getDefaultBranch();
    let thisBranch = this.vhQuerySales.getlocalBranch(this.vhQuerySales.getDefaultBranch()._id);
    if (branchDefault.name != thisBranch.name) {
      this.vhQuerySales.clearDefaultBranch();
      this.vhQuerySales.setDefaultBranch(thisBranch._id);
      branchDefault = this.vhQuerySales.getDefaultBranch();
    }
    return branchDefault;
  }



  /**
   * Thay i chi nhnh v m popup xc nhn v router qua trang login.
   */
  changeBranch() {
    this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", this.languageService.translate("Are you sure to change branch") + "?", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhQuerySales.clearDefaultBranch();
        this.router.navigate(['/login'], { state: { change_branch: true } })
      }).catch((err) => {
        console.log(err);
      });
  }

}
