  /**
   * Khi click chn thm sn phm vo ho n
   * @param product sn phm c chn
   */
  chooseProduct(product: any) {
    console.log('chooseProduct', product);
    if (product) {
      // kim tra xem sn phm c l hay khng
      if (product.manylot && !product.id_lotproduct) {
        this.openModalChooseLot(product);
      }
      // kim tra sn phm c combo hay khng
      else if (product.combos && product.combos.filter(e => e.lots && !e.id_lotproduct).length) {
        this.tempProduct = product;
        this.isShowModalSelectLotsCombo = true;
      }
      else {
        // ly gi ca sn phm
        let price = this.default_price_type == 1 ? product.price : product.price2 ? product.price2 : product.price; // check 2 k l kiu gi bn v c gi bn s hay ko
        // Nu khng c Hin th mt sn phm trn nhiu dng
        if (!this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_product_mutil_line_sales) {
          this.addBillDetailItem({
            ...product,
            price: price,
            earning_points_product: this.billService.getTotalEarningPointProduct(product, null, this.selectedCustomerProgramProduct)
          });
        }
        else {
          this.addBillDetail({
            ...product,
            price: price,
            earning_points_product: this.billService.getTotalEarningPointProduct(product, null, this.selectedCustomerProgramProduct)
          });
        }
        // reset id l sau khi chn  ln sau li hin modal chn ln na
        product.id_lotproduct = '';
      }
    }

    // if (this.isDiscountBill) {
    //   this.getDiscountBillOfCustomer();
    // }
    // else {
    //   this.selectedBill.discount_bill = 0;
    // }
    // this.updateBill();

    if (!product) {
      this.isShowSearchProduct = false;
    }
  }



