  /**
   * Thm sn phm c chn vo bill detail, vi debounce 300ms.
   *
   * Nu  c sn phm trong bill th tng s lng ln 1, nu cha c th thm bill detail mi.
   *
   * @param product sn phm c chn
   * @example this.addBillDetailItem(product_selected);
   */
  addBillDetailItem(product: any) {
    const index = this.billService.getBillDetailIndex(product, this.billDetails);
    // Nu  c sn phm trong bill th tng s lng ln 1
    if (index != -1) {
      if (this.billDetails[index]['quantity'] < 9999999999) this.billDetails[index]['quantity'] += 1;
      this.updateBillCalculations();
      // Hy b timeout c nu c
      if (this.debounceAddBillDetailTimer) {
        clearTimeout(this.debounceAddBillDetailTimer);
      }
      // t timeout mi
      this.debounceAddBillDetailTimer = setTimeout(() => {
        this.billService.updateBillDetail(this.billDetails[index]._id, this.billService.getCleanBillDetail(this.billDetails[index]));
      }, 300); // i 300ms
    }
    // Nu cha c sn phm trong bill th thm bill detail mi
    else {
      this.addBillDetail(product);
    }
  }



