  /**
   * Thm bill detail mi vo bill, vi debounce 300ms.
   * @param product
   * @param new_price
   * @example  this.addBillDetail(product_selected);
   */
  addBillDetail(product: any, new_price?: number) {
    let detail: any = {
      name: product.name,
      id_product: product._id,
      quantity: 1,
      img: product.img,
      ptype: product.type,
      unit: product.unit,
      units: product.units ? product.units : [],
      ratio: product.ratio ? product.ratio : 1,
      price: new_price != null ? new_price : product.price,
      price2: product.price2,
      price_origin: product.price,
      earning_points_product: product.earning_points_product ? product.earning_points_product : 0,
      id_bill: this.selectedBill._id,
      time_start_order: new Date().toISOString(),
    };

    if (product.type == 5) {
      detail['cbtype'] = 5;
      detail['combos'] = [...product.combos.map(item => {
        let product: VhSaleDesktopInvoiceDetailCombo = {
          id_product: item.id_product,
          name: item.name,
          ptype: item.type,
          price: item.price,
          quantity: item.quantity,
          unit: item.unit,
          units: item.units,
          ratio: item.ratio ? item.ratio : 1,
          lots: item.lots ? item.lots : [],
          id_lotproduct: item.id_lotproduct,

        };
        if (item.lots) product['lots'] = item.lots;
        if (item.id_lotproduct) product['id_lotproduct'] = item.id_lotproduct;
        if (item.id_subproduct) product['id_subproduct'] = item.id_subproduct;
        return product;
      })];
    }
    if (product.lot_number) detail['lot_number'] = product.lot_number;
    if (product.lots) detail['lots'] = product.lots;
    if (product.id_lotproduct) detail['id_lotproduct'] = product.id_lotproduct;
    if (product.id_subproduct) detail['id_subproduct'] = product.id_subproduct;

    // Thm v mng hin th ln giao din
    this.billDetails.unshift(detail);
    this.updateBillCalculations();

    // Nu c bt T ng tr chut vo  s lng lc chn/qut m sn phm khi to ha n
    if (this.auto_active_quantity_input) {
      setTimeout(() => {
        const input = document.querySelector('app-bill-detail-item .input-number__input') as HTMLInputElement;
        if (input) {
          input.focus();
          input.select();
        }
      }, 0);
    }

    // Nu  nm trong queue th hy b
    if (this.addBillDetailQueue.has(product._id)) {
      clearTimeout(this.addBillDetailQueue.get(product._id));
    }
    // To timeout mi  cp nht ln database
    const timeout = setTimeout(() => {
      this.vhQuerySales.createBill_Detail(detail)
        .then((vhbill_detail) => {
          const index = this.billService.getBillDetailIndex(product, this.billDetails);
          this.billDetails[index]._id = vhbill_detail._id;
          this.updateBill();
          this.addBillDetailQueue.delete(product._id);
        }).catch(e => {
          console.log('error', e);
          this.addBillDetailQueue.delete(product._id);
        });
    }, 300); // i 300ms sau khi thm th mi cp nht ln database

    this.addBillDetailQueue.set(product._id, timeout);
  }



  /** Debounce timer  x l updateBill lin tc */
  get debounceUpdateBillTimer() {
    return this.salesSapoSharedService.debounceUpdateBillTimer;
  }
