  /**
   * xa bill, nhn t sk xa tab
   * @param index v tr bill_detail cn xo
   * @example this.deleteBill(1);
   */
  deleteBill(index: number) {
    this.billService.deleteBill(index, this.listBills[index]._id, this.selectedIndex)
      .then(() => {
        this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Invoice deleted successfully!"));
      })
      .catch((error: any) => {
        console.log('error', error);
      })
      .finally(() => {
        this.vhComponent.hideLoading(0);
        setTimeout(() => {
          this.listBills.splice(index, 1);

          if (!this.listBills.length) {
            this.newBill();
          }
          else {
            // Nu xo bill trc bill ang chn th gim index, bill ang chn th vn giu nguyn nn khng cn selectBill li
            if (index < this.selectedIndex) {
              this.selectedIndex--;
            }
            // Nu xo bill ang chn
            else if (index == this.selectedIndex) {
              // Nu l bill cui th update li index l length - 1
              if (index >= this.listBills.length - 1) {
                this.selectedIndex = this.listBills.length - 1;
              }
              // Nu khng phi bill cui th vn gi nguyn index
              else {
                this.selectedIndex = index;
              }
              this.selectBill(this.listBills[this.selectedIndex], this.selectedIndex);
            }
          }
        }, 0);
      });
  }



  /**
   * Xo bill detail
   * @param detail bill_detail cn xo
   * @param index index ca bill_detail cn xo
   */
  deleteBillDetail(detail: VhSaleInvoiceDetailEdit, index?) {
    // Hm gi sau khi xo thnh cng, s gi sau khi xo trn giao din hoc sau khi xo trn database tht bi
    const updateSubtotalAndDiscount = () => {
      this.updateBillCalculations();
      this.updateBill();
    };
    // update trn giao din
    const removedItem = this.billDetails.splice(index, 1)[0];
    updateSubtotalAndDiscount();
    // update trn database
    this.vhQuerySales.deleteBill_Detail(detail._id)
      .then((bool: any) => {
      })
      .catch((error) => {
        console.log(error);
        // Nu c li th restore li
        this.billDetails.splice(index, 0, removedItem);
        updateSubtotalAndDiscount();
      });

    if (!this.isType7GiftSelected) this.handlePromotionType7(this.promotionsType7);
  }



  /**
   * Xo tt c sn phm trong bill
   */
  deleteAllBillDetail() {
    // Hm gi sau khi thc thi thnh cng, s gi sau khi thc thi trn giao din hoc sau khi thc thi trn database tht bi
    const updateSubtotalAndDiscount = () => {
      this.updateBillCalculations();
    };

    // update trn giao din
    const removedItems = [...this.billDetails];
    this.billDetails = [];
    updateSubtotalAndDiscount();
    // update trn database
    this.vhQuerySales.updateBill_Billdetail(this.selectedBill._id, this.selectedBill, [])
      .then((bool: any) => {
      })
      .catch((error) => {
        console.log(error);
        // Nu c li th restore li
        this.billDetails = removedItems;
        updateSubtotalAndDiscount();
        this.updateBill();
        this.mess.error("Error");
      });

    if (!this.isType7GiftSelected) this.handlePromotionType7(this.promotionsType7);
  }



