  /**
   * bt sk thay i s lng sp trong bill
   * @param product
   * @param event : gi tr thay i
   * @example this.checkQuantityProduct(bill_detail_selected, 1)
   */
  checkQuantityProduct(product: VhSaleInvoiceDetailEdit, event) {
    if (!event || event <= 0) {
      product.quantity = 1;
    }
    else {
      product.quantity = product.quantity ? parseFloat(parseFloat(event).toFixed(2)) : 1;
    }

    this.updateBillCalculations();
    if (!this.isType7GiftSelected) this.handlePromotionType7(this.promotionsType7);
  }



  /**
   * Cp nht ln database sau khi thay i s lng sn phm
   */
  updateQuantityProduct(product: VhSaleInvoiceDetailEdit) {
    this.billService.updateBillDetail(product._id, this.billService.getCleanBillDetail(product));
    this.updateBill();
  }



