  /**
   * Tnh li Cng tin ca bill khi c s thay i bill_detail
   *
   * @example this.setSubTotal()
   */
  setSubTotal() {
    this.subTotal = this.billService.getSubTotal(this.billDetails);
  }



  /**
   * Tr v tng tin
   * @returns number
   * @example let total = this.getTotalATax();
   */
  getTotalATax(): number {
    return this.selectedBill.total + this.taxValue;
  }



  /**
   * Tnh li tng tin ca bill  lu trng total.
   *
   * Gi khi c s thay i v: subTotal, discount, discount_bill, payment_points, payment_coupons, fee, tax.
   *
   * Tnh li trng: total, tax, payment, cash
   */
  setTotal() {
    this.onChangePercentDiscount(this.percentDiscountBill);
    this.selectedBill.total = this.billService.getTotal(this.subTotal, this.selectedBill);
    this.vhQuerySales.writeBill_BillDetails_toDisplayLEDScreens(this.selectedBill, this.billDetails);
    this.taxValue = this.billService.getTax(this.selectedBill);
    this.selectedBill.payment = this.billService.getPayment(this.selectedBill, this.taxValue);
    this.selectedBill.cash = this.billService.getCash(this.selectedBill, this.selectedBill.cash);
  }



  /**
   * Khi thay i gim gi bill theo %
   * @param value % gim
   * @example this.onChangePercentDiscount(10);
   */
  onChangePercentDiscount(value) {
    if (parseFloat(value) > 100) {
      document.querySelector(".percent-discount-price input")['value'] = 0;
    }
    else {
      this.percentDiscountBill = value;
    }
    this.selectedBill.discount = this.subTotal * (this.percentDiscountBill / 100);
  }



  /**
   * Tnh Tng im tch lu cng thm ca bill ( = earningPointAllProduct + EarningPointBill )
   * @example let earningPoint = this.getTotalEarningPoint();
   * @returns number
   */
  getTotalEarningPoint() {
    this.earningPoint = this.billService.getTotalEarningPoint(this.selectedCustomerProgramProduct, this.selectedCustomerProgramBill, this.subTotal, this.selectedBill, this.billDetails);
    return this.earningPoint ? this.earningPoint : 0;
  }



  /**
   * Tnh li chit khu ha n
   */
  getDiscountBillOfCustomer() {
    this.selectedBill.discount_bill = this.billService.getDiscountBillByCustomer(this.selectedBill, this.subTotal);
  }



  /**
   * Tnh li tt c thng s  Bill.
   *
   * Tnh li subTotal, chit khu, discount, discount_bill, payment_points, payment_coupons, fee, tax, im tch ly cng thm
   */
  updateBillCalculations() {
    this.setSubTotal();
    if (this.isDiscountBill) {
      this.getDiscountBillOfCustomer();
    }
    else {
      this.selectedBill.discount_bill = 0;
    }
    this.setTotal();
    this.getTotalEarningPoint();
  }



