  /**
   * Khi thay i Loi khch hng
   */
  changeBusiness(event) {
    this.addCustomerForm.get('isBusiness').setValue(event?.value, { emitEvent: false });
    // taxcode required khi isBusiness = true
    if (event?.value) {
      this.addCustomerForm.get('taxcode')?.setValidators([Validators.required]);
      this.addCustomerForm.get('taxcode')?.updateValueAndValidity();
    } 
    else {
      this.addCustomerForm.get('taxcode')?.clearValidators();
      this.addCustomerForm.get('taxcode')?.updateValueAndValidity();
    }
  }



  /**
   * Thm khch hng mi
   */
  addCustomer() {
    let customer: any = { ...this.addCustomerForm.value };
    const phoneNumberValue = customer.phone;
    customer.countryCode = this.countryCode;
    customer.phone = this.functionService.getNationalPhoneNumber(phoneNumberValue, this.countryCode);
    customer.phoneNumber = this.functionService.getInternationalPhoneNumber(phoneNumberValue, this.countryCode);

    // Khch l
    if (this.customerType === 1) {
      this.selectedBill.retail_name = customer.name;
      this.selectedBill.retail_phone = this.vhAlgorithm.vhphoneNumber('.phone input').getRawValue();
      this.selectedBill.retail_address = customer.address;
      this.vhQuerySales.updateBill(this.selectedBill._id, { id_customer: 'id_retail', retail_name: this.selectedBill.retail_name, retail_phone: this.selectedBill.retail_phone, retail_address: this.selectedBill.retail_address });
      this.isShowModalAddCustomer = false;
    }
    // Thnh vin
    else {
      this.createCustomerStatus = true;
      customer.date = (new Date()).toISOString();

      if(customer.isBusiness) {
        delete customer.gender;
        delete customer.datebirth;
      }
      else {
         delete customer.taxcode;
      }
      
      this.vhQuerySales.addCustomer(customer).then((res) => {
        this.customerList.push(res);
        setTimeout(() => {
          this.vhComponent.showToast(1500, `${this.languageService.translate("Customer")} ${customer.name} ${this.languageService.translate("has been added successfully")}`, "success-toast")
        }, 500)
      }, (error) => {
        console.log(error);
        this.vhComponent.showToast(1500, this.languageService.translate("An error occurred while adding customer. Please try again"), "alert-toast")
      }).finally(() => {
        this.createCustomerStatus = false;
        this.isShowModalAddCustomer = false;
      });
    }
  }



  /**
   * S kin thay i khch hng c chn
   * @param id_customer id ca khch hng c chn
   */
  changeCustomer(id_customer) {
    this.selectedBill.id_customer = id_customer;
    const localCustomer = this.vhQuerySales.getlocalCustomer(id_customer);
    this.selectedBill.tax_buyerDisplayName = localCustomer?.name || '';
    this.selectedBill.tax_buyerLegalName = localCustomer?.name || '';
    this.selectedBill.tax_buyerTaxCode = localCustomer?.taxcode || '';
    this.selectedBill.tax_buyerAddressLine = localCustomer?.address || '';
    this.selectedBill.tax_buyerEmail = localCustomer?.email || '';

    if (this.selectedBill.retail_name) this.clearRetailCustomer();
    let debt = this.vhQuerySales.getlocalCustomer(id_customer).debt_enable;
    this.changeDebt(debt ? debt : false);
    this.updateServiceTime();
    this.handleChangePaymentType();
    this.resetDiscountCouponAndPoint();
    this.getClassAndProgramFirstLoad(id_customer);
    this.updateBillCalculations();
    this.updateBill();
  }



  /**
   * Khi to khch hng v danh sch khch hng
   */
  initCustomer() {
    this.customerList = this.customerService.getCustomerList();
    if (this.selectedBill?.id_customer == 'id_retail') {
      this.customerType = 1;
    } else {
      this.customerType = 2;
    }
    this.pointValidity = this.vhQuerySales.getlocalCustomerPoints(this.selectedBill.id_customer);
  }



  /**
   * Clear thng tin khch l
   */
  clearRetailCustomer() {
    this.selectedBill.retail_address = '';
    this.selectedBill.retail_name = '';
    this.selectedBill.retail_phone = '';
  }



  /**
   * khi thay i khch hng s check xem khch  c n hay ko, nhn output ny t modal chn khch hng
   * @param bool
   */
  changeDebt(bool) {
    this.debt = bool;
    if (!bool) {
      this.selectedBill.payment = this.getTotalATax();
    }
  }



  /**
   * Thay i loi khch hng
   * 1 : khch l - id_customer: 'id_retail'
   * 2 : member
   */
  changeCustomerType(type) {
    this.customerType = type;
    if (type == 1) {
      this.selectedBill.id_customer = 'id_retail';
      this.selectedBill.tax_buyerDisplayName = 'Ngi mua khng ly ho n';
      this.selectedBill.tax_buyerLegalName = '';
      this.selectedBill.tax_buyerTaxCode = '';
      this.selectedBill.tax_buyerAddressLine = '';
      this.selectedBill.tax_buyerEmail = '';
      this.changeDebt(false);
      this.updateServiceTime();
      this.handleChangePaymentType();
      this.resetDiscountCouponAndPoint();
      this.getClassAndProgramFirstLoad(this.selectedBill.id_customer);
      this.updateBillCalculations();
      this.updateBill();
    }
    else if (type == 2) {
      if (!this.selectedBill.retail_name) {
        this.customerList = this.customerService.getCustomerList();
        setTimeout(() => {
          document.getElementById('select-customer')?.click();
          this.cdr.detectChanges();
        }, 200);
      }
      else {
        this.nzModalService.confirm({
          nzTitle: `${this.languageService.translate('Retail customer information will be lost')}?`,
          nzCancelText: this.languageService.translate('Cancel'),
          nzOkText: 'Ok',
          nzOnOk: () => {
            this.clearRetailCustomer();
            this.customerList = this.customerService.getCustomerList();
            setTimeout(() => {
              document.getElementById('select-customer')?.click();
              this.cdr.detectChanges();
            }, 200);
            this.callbackAfterOKResetRetailCustomer();
          },
          nzOnCancel: () => {
            this.customerType = 1;
          },
        });
      }
    }
  }

  /**
   * Sau khi nhn OK trong modal confirm reset khch l (chuyn t Khch l -> Thnh vin)
   */
  callbackAfterOKResetRetailCustomer() { }




