  /**
   * Bt sk thay i date ca ha n, gn li gi tr date mi cho n
   * @example this.setDate('2023-06-12T08:02:27.697Z')
   */
  setDate(event) {
    if (new Date(event).getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
      this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the registration date (")} ${this.functionService.formatDate(this.vhAuth.getStore().registrationdate)} ${")"}`, 5000)
      this.selectedBill.date = new Date(this.selectedBill.date);
    }
    else
      this.vhQuerySales.getValidityDate(new Date(event)).then((validitydate: any) => {
        if (validitydate == null) {
          this.selectedBill.date = event;
          this.updateBill();
        }
        else {
          this.selectedBill.date = new Date(this.selectedBill.date);
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.functionService.formatDate(validitydate)}`, 5000)
        }
      });
  }



  /**
   * Khi thay i Ph
   */
  onChangeFee() {
    if (!document.querySelector('.bill-fee input')) return;
    this.cleaveJsService.destroyCleave('.bill-fee input');
    let fee: any = this.cleaveJsService.getRawValue('.bill-fee input');
    this.selectedBill.fee = fee ? parseFloat(fee) : 0;
  }



  /**
   * Khi thay i gim gi
   */
  onChangeDiscount() {
    let discount: any = this.cleaveJsService.getRawValue('.discount-price input');
    this.selectedBill.discount = (discount > 0 && discount < this.subTotal ? parseFloat(discount) : 0);
    this.percentDiscountBill = this.subTotal ? (this.selectedBill.discount / this.subTotal) * 100 : 0;
  }



  /**
   * Khi thay i Tin khch a
   */
  onChangeCash() {
    if (!document.querySelector('.cash input')) return;
    let cash: any = this.cleaveJsService.getRawValue('.cash input');
    this.selectedBill.cash = parseFloat(cash);
  }



  /**
   * Khi thay i Tin khch tr
   */
  onChangePayment() {
    if (this.debt) {
      if (!document.querySelector('.payment input')) return;
      let payment: any = this.cleaveJsService.getRawValue('.payment input');
      let value = payment ? parseFloat(payment) : 0;
      this.selectedBill.payment = (value ? (value > this.getTotalATax() || value < 0 ? this.getTotalATax() : value) : 0);
      document.querySelector('.payment input')['value'] = this.selectedBill.payment;
    }
  }



