  /**
   * kim tra m coupon hp l hay ko, tnh s tin thanh ton theo coupon tng ng
   * @param code
   * @example getDiscountOfCoupon('KDJWOIHA')
   */
  getDiscountOfCoupon(code) {
    if (code) {
      this.vhQuerySales.getCoupon_Code_Name_Release_byCode(code)
        .then((product_codes: any) => {
          if (product_codes && !this.listSelectedCoupons.find(item => item.code == code)) {
            switch (product_codes.status) {
              case 1: {
                this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Invoice deleted successfully"));
                break;
              }
              case 3: {
                if (new Date() < new Date(product_codes.date_expire)) {
                  this.listSelectedCoupons.push(product_codes);
                  this.selectedBill.payment_coupons = product_codes.value;
                  this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Coupon used successfully"));
                }
                else {
                  this.vhComponent.alertMessageDesktop("error", this.languageService.translate("This coupon has expired"), 8000);
                }
                break;
              }
              case 4: {
                this.vhComponent.alertMessageDesktop("error", this.languageService.translate("This coupon has been used"), 8000);
                break;
              }
            }
          }
          if (!product_codes) {
            this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Coupon not found"), 8000);
          }
          setTimeout(() => {
            document.getElementById('coupon').getElementsByTagName("input")[0].value = '';
          }, 200);
        });
    }
  }



  /**
   * xa coupon  thm, tnh li tin thanh ton bng coupon
   * @param item
   * @example this.onDeleteProductCode(coupon_selected)
   */
  onDeleteProductCode(item) {
    this.listSelectedCoupons.splice(this.listSelectedCoupons.findIndex(i => i._id == item._id), 1);
    this.selectedBill.payment_coupons -= item.value;
  }



  /**
   * tnh li gi tr thanh ton bng tin vi s im s dng tng ng trong chng trnh tch im
   * @param value im s dng
   * @example this.calculatePoint2Money(10)
   */
  calculatePoint2Money(value) {
    if (value <= this.pointValidity) {
      this.valueDiscountPoint = value;
      this.selectedBill.payment_points = ((value / this.selectedCustomerClass.payment.points) * this.selectedCustomerClass.payment.money);
      this.selectedBill.use_points = value;
    }
    else {
      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Exceeded the allowable points limit"))
    }
  }



  /**
   * Reset li gi tr im v coupon
   */
  resetDiscountCouponAndPoint() {
    this.selectedBill.payment_coupons = 0;
    this.selectedBill.payment_points = 0;
    this.selectedBill.use_points = 0;
    this.valueDiscountPoint = 0;
    this.listSelectedCoupons = [];
    this.selectedCustomerProgramBill = null;
    this.selectedCustomerProgramProduct = null;
    this.pointValidity = 0;
    this.earningPoint = 0;
  }



