  /**
   * Khi to phng thc thanh ton
   */
  initPaymentType() {
    // Ly danh sch ti khon
    this.listWallets = this.vhQuerySales.getlocalWallets()
      .filter(wallet => wallet._id != 'id_cash' && wallet.id_branch == this.vhQuerySales.getDefaultBranch()._id)
      .map(wallet => { return { label: wallet.name, value: wallet._id, isLeaf: true } });
    // gn danh sch ti khon vo phng thc thanh ton (3)Ti khon
    this.nzOptions[2].children = this.listWallets;
  }


  /**
   * Khi thay i phng thc thanh ton
   * @param event value ca cascader c chn. V d: nu khng c children th l [1] hoc [2], nu c children th l [3, id_wallet]
   */
  onChangePaymentType(event) {
    if (event[0] == 1 || event[0] == 2) {
      this.setPaymentType(event[0]);
    }
    else if (event[0] == 3) {
      this.setPaymentType(event[0], event[1]);
    }
  }


  /**
   * Cp nht payment_type cho bill
   * @param type 1-cash-Tin mt, 2-debit-Cng n, 3-account-Ti khon
   * @param id_wallet id ca Ti khon
   * @example this.setPaymentType(1);
   */
  setPaymentType(type: 1 | 2 | 3, id_wallet?) {
    this.selectedBill.payment_type = type;

    switch (type) {
      case 1: {
        this.selectedBill.id_wallet = 'id_cash';
        this.selectedBill.payment = this.selectedBill.payment ? this.selectedBill.payment : this.getTotalATax();
        this.updateBill();
        break;
      }
      case 2: {
        this.selectedBill.payment = 0;
        this.selectedBill.id_wallet = null;
        this.selectedBill.cash = 0;
        this.updateBill();
        break;
      }
      case 3: {
        if (id_wallet) {
          this.selectedBill.id_wallet = id_wallet;
          this.selectedBill.payment = this.selectedBill.payment ? this.selectedBill.payment : this.getTotalATax();
          this.updateBill();
          break;
        }
      }
    };
  }



  /**
   * Gi khi thay i selectedBill.id_customer.
   * S kim tra xem c Cho php n hay khng, nu (KHNG cho php n || Khch l) th s disable phng thc thanh ton l (2)Cng n v set li phng thc thanh ton l (1)Tin mt
   */
  handleChangePaymentType() {
    const isDebtEnabled = this.vhQuerySales.getlocalCustomer(this.selectedBill.id_customer).debt_enable;
    const isRetailCustomer = this.selectedBill.id_customer === "id_retail";
    this.nzOptions[1].disabled = !isDebtEnabled;
    if (isRetailCustomer || !isDebtEnabled) {
      this.nzOptions[1].disabled = true;
      this.setPaymentType(1);
    }
  }



