  /**
   * Khi OK hay Cancel modal chn gi bn
   * @event gi bn c chn, null l cancel modal, 1 l gi bn l, 2 l gi bn bun/bn s
   */
  changePriceType(event) {
    this.isShowModalChangePriceType = false;

    if (event != null) { // nu c thay i kiu gi bn th cp nht li gi cc sn phm thnh gi bn s
      this.selectedBill.price_type = event;
      this.default_price_type = event;
      this.deletePromotion()
        .then(() => {
          if (this.billDetails.length) { // nu c length mi update
            for (let i in this.billDetails) {
              // Nu l dch v thi gian
              if (this.billDetails[i].ptype === VhType.SERVICETIME) {
                this.productService.updateServiceTimePrice(this.billDetails[i], event);
                delete (this.billDetails[i] as any).serviceTimePrice;
                // Cp nht li sn phm trong danh sch ha n
                this.billDetails[i] = { ...this.billDetails[i] };
                continue;
              }
              let price_update = 0;
              let product = this.vhQuerySales.getlocalDetailProduct(this.billDetails[i].id_subproduct ? this.billDetails[i].id_subproduct : this.billDetails[i].id_product);
              product = { ...product, ...this.vhQuerySales.getUnit_byRatio(product.units, this.billDetails[i].ratio) };
              if (event == 1) {
                price_update = product.price;
              }
              else {
                price_update = product.price2 ? product.price2 : product.price;
              }
              this.billDetails[i].price = price_update;
              this.billDetails[i].price_origin = price_update;
            }
            setTimeout(() => {
              this.updateBillCalculations();
            }, 400);

            this.vhQuerySales.updateBill_Billdetail(this.selectedBill._id, this.selectedBill, this.billDetails).then((bool: any) => {
              // console.log(bool);
            }).catch((error) => {
              console.log(error);
            });
          }
        });
    }
  }



