  editServiceTime(item) {
    this.selectedServiceTime = item;
    // Nu l Dch v thi gian
    this.productService.updateServiceTimePrice(this.selectedServiceTime, this.default_price_type);
    if(!this.selectedServiceTime.is_paused) {
      this.updateBillCalculations();
    }
    this.isShowModalEditServiceTime = true;
  }



  /**
   * Khi ng modal chnh sa Dch v thi gian
   */
  saveServiceTime() {
    if(this.selectedServiceTime.ptype === VhType.SERVICETIME) {
      if (!this.selectedServiceTime.is_paused) {
        this.selectedServiceTime.time_end_order = new Date();
      }
      const serviceTimePrice: any = this.productService.calculateServiceTimePrice(this.selectedServiceTime, this.endTime, this.default_price_type);
      this.selectedServiceTime.price = serviceTimePrice.total_price;
      this.selectedServiceTime.price_origin = serviceTimePrice.total_price;
      delete this.selectedServiceTime.serviceTimePrice;
    }

    this.selectedServiceTime.price = parseFloat(this.selectedServiceTime.price)
    this.isShowModalEditServiceTime = false;

    let index: number = this.billDetails.findIndex(item => item._id == this.selectedServiceTime._id);
    this.billDetails[index] = { ...this.selectedServiceTime }
    this.updateBillCalculations();
    this.vhQuerySales.updateBill_Detail(this.selectedServiceTime._id, { quantity: this.selectedServiceTime.quantity, price: this.selectedServiceTime.price, ratio: this.selectedServiceTime.ratio }).then((res) => {
    });
  }



  /**
   * Tnh li bill detail cho Dch v thi gian v setSubTotal() cho invoices
   */
