  /**
   * M modal Chnh sa thi gian s dng
   */
  handleOpenEditUsedTime() {
    this.tempStartTime = this.selectedServiceTime.time_start_order;
    if (!(this.tempStartTime instanceof Date)) {
      this.tempStartTime = new Date(this.tempStartTime);
    }
    if(this.selectedServiceTime.is_paused) {
      this.tempEndTime = this.selectedServiceTime.time_end_order;
      if (!(this.tempEndTime instanceof Date)) {
        this.tempEndTime = new Date(this.tempEndTime);
      }
    }
    else {
      this.tempEndTime = null;
    }
    this.isShowModalEditUsedTime = true;
  }



  /**
   * Khi nhn ok trong modal Chnh sa thi gian s dng
   */
  handleUpdateUsedTime() {
    // validate start time<= now; end time > start time
    if (this.tempStartTime > new Date()) {
      this.vhComponent.alertMessageDesktop('error', this.languageService.translate('The start time must be less than or equal to the current time.'));
      return;
    }
    if (this.tempEndTime && this.tempEndTime <= this.tempStartTime) {
      this.vhComponent.alertMessageDesktop('error', this.languageService.translate('The end time must be greater than the start time.'));
      return;
    }

    this.isShowModalEditUsedTime = false;

    this.selectedServiceTime.time_start_order = this.tempStartTime;

    const currentTime = new Date();
    if(!this.tempEndTime) {
      this.selectedServiceTime.is_paused = false;
    }
    else {
      // mt hng tnh gi s dng tnh gi ngay ti thi im 
      this.selectedServiceTime.is_paused = true;
    }

    this.selectedServiceTime.time_end_order = this.tempEndTime ?? currentTime;

    this.vhQuerySales.updateBill_Detail(this.selectedServiceTime._id, { 
      time_start_order: this.tempStartTime,
      time_end_order: this.tempEndTime ? this.selectedServiceTime.time_end_order : null,
      is_paused: this.selectedServiceTime.is_paused,
    });

    // Tnh li gi cho dch v thi gian
    const serviceTimePrice: any = this.productService.calculateServiceTimePrice(this.selectedServiceTime, this.endTime, this.default_price_type);
    this.selectedServiceTime.serviceTimePrice = JSON.parse(JSON.stringify(serviceTimePrice));
    this.selectedServiceTime.price = serviceTimePrice.total_price;
    this.selectedServiceTime.price_origin = serviceTimePrice.total_price;
    this.updateBillCalculations();
  }



  /**
   * Tm ngng / Tip tc tnh gi
   * 
   * @param isPause true: tm ngng; false: tip tc tnh gi
   */
  pauseTime(isPause: boolean) {
    this.selectedServiceTime.time_end_order = new Date();
    this.selectedServiceTime.is_paused = isPause;

    const serviceTimePrice: any = this.productService.calculateServiceTimePrice(this.selectedServiceTime, this.endTime, this.default_price_type);
    this.selectedServiceTime.serviceTimePrice = JSON.parse(JSON.stringify(serviceTimePrice));
    this.selectedServiceTime.price = serviceTimePrice.total_price;
    this.selectedServiceTime.price_origin = serviceTimePrice.total_price;
    this.updateBillCalculations();

    this.vhQuerySales.updateBill_Detail(this.selectedServiceTime._id, { 
      time_end_order: isPause ? this.selectedServiceTime.time_end_order : undefined, 
      is_paused: isPause ,
      price: this.selectedServiceTime.price,
    });
  }





